@import url(https://fonts.googleapis.com/css2?family=Brygada+1918:ital,wght@1,600&display=swap);
* {
  padding: 0;
  margin: 0;
  font-family: "Ubuntu", sans-serif;
  box-sizing: border-box;
}

a {
  color: white;
  text-decoration: none;
}
.navbar {
  width: 100%;
  height: 10vh;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-end;
  padding-right: 5rem;
  background-color: #181818;
}

.navbar > h3 > a {
  margin: 2rem;
  color: #f5d10d;
}

@media screen and (max-width: 765px) {
  .navbar > h3 > a {
    margin: 1.5rem;
    font-size: 1.05rem;
  }

  .navbar {
    padding-right: 3rem;
  }
}

@media screen and (max-width: 633px) {
  .navbar > h3 > a {
    margin: 1.2rem;
    font-size: 1rem;
  }

  .navbar {
    padding-right: 2.5rem;
  }
}

@media screen and (max-width: 544px) {
  .navbar > h3 > a {
    margin: 1rem;
    font-size: 1rem;
  }

  .navbar {
    padding-right: 1.8rem;
  }
}

@media screen and (max-width: 433px) {
  .navbar > h3 > a {
    margin: 0.7rem;
    font-size: 0.9rem;
  }

  .navbar {
    padding-right: 1.3rem;
  }
}

.home {
  background-color: #181818;
  height: 90vh;
  width: 100%;
}

.home .content {
  display: flex;
  height: inherit;
  padding-left: 4rem;
}

.image-container {
  width: 25%;
  border: 1rem white;
  display: flex;
  justify-content: center;
  align-items: center;
}

.profile-picture {
  width: 10rem;
  height: 17rem;
  overflow: hidden;
  border-radius: 10px;
}

.profile-picture > img {
  width: inherit;
  height: inherit;
  overflow: hidden;
}

.intro {
  display: flex;
  flex-direction: column;
  justify-content: center;
  /* border: 1px solid white; delet later */
  flex-grow: 1;
}

.intro > h1 {
  font-size: 3rem;
  color: #8cbdd6;
  letter-spacing: 4px;
}

.intro > h6 {
  font-size: 1.5rem;
  color: #396a85;
  font-family: "Brygada 1918", serif;
  font-style: italic;
  margin-top: 0.6rem;
}

.home .buttons-wrapper button {
  width: 7.2rem;
  height: 2.8rem;
  background-color: #e31c25;
  border-style: none;
  color: white;
  font-weight: 500;
  letter-spacing: 2px;
  border-radius: 5px;
  margin: 3rem 0rem;
  text-align: center;
}

.home .buttons-wrapper button.resume {
  background-color: #41875D;
  margin-left: 1rem;
}

.home .iconify {
  margin-bottom: -3px;
}

.social-media-icons {
  display: flex;
  width: 10%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 2.5rem;
  color: #e31c25;
}

.social-media-icons > a {
  margin: 1rem;
  color: #e31c25;
}

.vertical-line {
  border-left: 2px solid #e31c25;
  height: 15rem;
}

@media screen and (max-width: 1158px) {
  .profile-picture {
    width: 12rem;
    height: 12rem;
    margin: 0.5rem;
  }
}


@media screen and (max-width: 1000px) {
  .image-container {
    margin-right: 1rem;
  }
  
  .intro > h1 {
    font-size: 2.4rem;
  }

  .intro > h6 {
    font-size: 1.7rem;
  }
}

@media screen and (max-width: 916px) {
  .intro > h1 {
    font-size: 2.1rem;
    letter-spacing: 3px;
  }

  .intro > h6 {
    font-size: 1.5rem;
  }

  .profile-picture {
    width: 9.5rem;
    height: 9.5rem;
  }

  .home .buttons-wrapper button {
    width: 6.5rem;
    height: 2.4rem;
    margin-top: 1rem;
    margin-bottom: 1rem;
  }
}

@media screen and (max-width: 765px) {
  .intro > h1 {
    font-size: 1.6rem;
    letter-spacing: 2px;
  }

  .intro > h6 {
    font-size: 1.2rem;
  }

  .profile-picture {
    width: 8.1rem;
    height: 8.1rem;
  }

  .home .content {
    padding-left: 1rem;
  }
}

@media screen and (max-width: 633px) {
  .social-media-icons {
    width: 3.2rem;
    font-size: 1.9rem;
  }

  .home .buttons-wrapper button {
    width: 5.5rem;
    height: 2.1rem;
    margin-top: 1rem;
    margin-bottom: 1rem;
    font-size: .8rem;
    letter-spacing: 1px;
  }

  .buttons-wrapper .resume svg {
    margin-left: -4px;
  }

  .profile-picture {
    width: 8rem;
    height: 8rem;
  }
}

@media screen and (max-width: 544px) {
  .profile-picture {
    width: 7rem;
    height: 7rem;
  }

  .intro > h1 {
    font-size: 1.4rem;
    letter-spacing: 2px;
  }

  .intro > h6 {
    font-size: 1rem;
  }

  .home .buttons-wrapper button {
    width: 5rem;
    height: 2rem;
    letter-spacing: 0px;
    font-weight: 400;
  }
}


@media screen and (max-width: 498px) {
  .social-media-icons {
    width: 2.8rem;
    font-size: 1.4rem;
    color: #e31c25;
  }

  .profile-picture {
    width: 6.1rem;
    height: 6.1rem;
  }
}

@media screen and (max-width: 433px) {
  .intro > h1 {
    font-size: 1.2rem;
    letter-spacing: 1px;
  }

  .intro > h6 {
    font-size: 0.8rem;
    font-weight: 300;
  }

  .home .buttons-wrapper button {
    width: 4.4rem;
    height: 1.7rem;
    font-weight: 300;
  }

  .profile-picture {
    width: 5.6rem;
    height: 5.6rem;
  }
}

@media screen and (max-width: 400px) {
  .intro > h1 {
    font-size: 1.2rem;
    letter-spacing: 0.5px;
  }

  .intro > h6 {
    font-size: 0.8rem;
    font-weight: 300;
  }

  .profile-picture {
    width: 5rem;
    height: 5rem;
  }

  .home .buttons-wrapper button {
    width: 4.31rem;
    height: 1.61rem;
    font-weight: 300;
  }

  .social-media-icons {
    width: 2.2rem;
    font-size: 1.2rem;
  }
}
.about {
  background-color: #3C3C3C;
  width: 100%;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 3rem 3rem;
  padding-bottom: 5rem;
}

.about .title {
  height: 10%;
  text-align: center;
  color: #f5d10d;
}

.about .content {
  display: flex;
  height: 90%;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  text-align: center;
  color: white;
  font-size: 1.3rem;
}

.about .content .about-me {
  max-width: 45rem;
  line-height: 2.5rem;
  margin-top: 1.5rem;
}

.teckstacks {
  margin-top: 3rem;
  width: 40%;
}

.teckstacks-wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.teck-icon-wrapper {
  margin: 1rem;
}

.teck-icon-wrapper i {
  font-size: 2.8rem;
  border-radius: 50%;
  background-color: rgb(245, 245, 245);
  padding: 0.8rem;
}

.teck-icon-wrapper p {
  margin-top: 0.65rem;
  font-size: 1rem;
}

/* ///// */

.tools {
  margin-top: 3rem;
  width: 40%;
}

.tools-wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.tools-icon-wrapper {
  margin: 1rem;
}

.tools-icon-wrapper svg {
  width: 4.2rem;
  height: 4.2rem;
  border-radius: 50%;
  background-color: rgb(245, 245, 245);
  padding: 0.8rem;
  margin-bottom: -1rem;
  margin-top: -0.6rem;
}

.github path {
  fill: black;
}

.teckstacks > h2 {
  margin-bottom: 0.4rem;
}

.tools > h2 {
  margin-bottom: 0.4rem;
}

.tools-icon-wrapper p {
  margin-top: 0.7rem;
  font-size: 1rem;
}

@media screen and (max-width: 1394px) {
  .teckstacks, .tools {
    width: 50%;
  }

  .about .content {
    font-size: 1.2rem;
  }

  
}

@media screen and (max-width: 1134px) {
  .teckstacks, .tools {
    width: 60%;
  }
}

@media screen and (max-width: 961px) {
  .teckstacks, .tools {
    width: 80%;
  }

  .about .content {
    font-size: 1.1rem;
  }

  .about-me p{
    line-height: 1.5rem;
  }
}

@media screen and (max-width: 611px) {
  .teck-icon-wrapper i {
    font-size: 2rem;
  }

  .tools-icon-wrapper svg {
    width: 3.5rem;
    height: 3.5rem;
    border-radius: 50%;
    /* background-color: rgb(245, 245, 245); */
    /* padding: 0.8rem; */
    /* margin-bottom: -1rem; */
    /* margin-top: -0.6rem; */
  }

  .about .content {
    font-size: 1rem;
  }

  .about {
    padding: 1rem 1rem;
  }
}

@media screen and (max-width: 563px) {
  .teckstacks, .tools {
    width: 94%;
  }
  .teck-icon-wrapper i {
    font-size: 1.7rem;
  }

  .teckstacks {
    margin-top: 2rem;
  }

  .tools {
    margin-top: 2rem;
  }

  .teckstacks > h2 {
    font-size: 1.5rem;
  }

  .tools > h2 {
    font-size: 1.5rem;
  }

  .teck-icon-wrapper {
    margin: 0.6rem 0.5rem;
  }

  .tools-icon-wrapper {
    margin: 0.6rem 0.5rem;
  }

}

@media screen and (max-width: 480px) {
  .teckstacks, .tools {
    width: 98%;
  }
}

@media screen and (max-width: 430px) {
  .teck-icon-wrapper i {
    font-size: 1.6rem;
  }

  .tools-icon-wrapper svg {
    width: 3.2rem;
    height: 3.2rem;
    border-radius: 50%;
  }
}
.projects {
  width: 100%;
  background-color: #181818;
  padding-bottom: 2rem;
}

.projects-title {
  text-align: center;
  padding-top: 3rem;
  color: #f5d10d;
}

.projects-wrapper {
  display: flex;
  flex-wrap: wrap;
  margin: 2rem;
  justify-content: center;
  align-items: center;
}

.project-container {
  width: 23rem;
  height: 18rem;
  display: flex;
  flex-direction: column;
  border-radius: 0.4rem;
  margin: 2rem;
  overflow: hidden;
  position: relative;
  background-color: rgb(255, 255, 255);
}

.project-container .img-wrapper {
  width: inherit;
  height: 12rem;
  position: relative;
}

.project-container .buttons-wrapper {
  width: inherit;
  height: inherit;
  position: absolute;
  top: 0rem;
  left: 0rem;
  background-color: #181818da;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  visibility: hidden;
}


.project-container .buttons-wrapper button {
  width: 6rem;
  height: 2.4rem;
  margin: .6rem;
  background-color: #5dc9ff;
  border-radius: 2.2rem;
  font-weight: 500;
  border: 2px solid white;
  box-shadow: 1px 1px 3px white;
}


.project-container .img-wrapper img{
  width: inherit;
  height: inherit;
  border-bottom: 2px solid rgb(162, 255, 243);
  box-shadow: 0px 1px 5px rgb(165, 255, 243);
}

.project-container .details {
  height: 6rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding-bottom: 0.4rem;
  padding-left: 0.6rem;
  margin-right: 0.3rem;
}

.project-container .details p {
  margin-left: 1.5rem;
  font-size: .9rem;
}

.project-container .details span {
  font-size: 1.4rem;
  margin-left: 1.5rem;
}

.project-container .details span i{
  margin-right: 8px;
}

.project-container:hover .buttons-wrapper {
  visibility: visible;
}

.project-container:hover {
  box-shadow: 1px 1px 10px #5dc9ff;
  border: 1px solid #5dc9ff;
}

@media screen and (max-width: 510px) {
  .projects-wrapper {
    margin: 0.2rem;
  }
  .project-container {
  margin: 0.8rem;
  }
}
